<template>
	<div class="login_face">
		<div class="login_box">
			<div class="img_box"></div>
			<!-- 登录表单 -->
			<div class="login_msg">
				<!-- 账号输入框 -->
				<el-input placeholder="输入账号" v-model="loginform.username" clearable id="username"></el-input>
				<!-- 密码输入框 -->
				<el-input placeholder="输入密码" v-model="loginform.password"  id="password" show-password></el-input>
				<!-- 登录按钮 -->
				<el-button id="btn" type="primary" @click="login()">登录</el-button>
                <!-- 注册按钮 -->
				<el-button id="btn" type="primary" @click="login()">注册</el-button>
			</div>
		</div>
	</div>
</template>


<style>
	.login_msg{
		width: 400px;
		height: 300px;
		margin: auto;/* 垂直和水平居中 */
		margin-top: 25px;
		
		/* background-color: lightcyan; */	
	}
	#username,#password{
		width:400px;
		height: 50px;
		margin: 15px;
		margin:15px 0;
		background: rgba(255, 255, 255, 0.65);
		border-radius: 25px;
	}
	#btn{
		width: 200px;
		margin-left: 90px;
		margin-top: 25px;
		border-radius: 40px;
	}

	* {
		margin: 0px;
		padding: 0px;
	}

	.login_face {
		display: flex;
		height: 100vh;
		align-items: center;
		justify-content: center;
		background-image: url(../assets/img8.png);
		background-size: cover;
		background-size: cover; /* 确保图片覆盖整个容器 */
		background-position: center; /* 图片在容器中居中 */
	}

	.login_box {
		width: 550px;
		height: 450px;
		border-radius: 20px;
		background: rgba(255, 255, 255, 0.38);
	}

	.img_box {
		width: 130px;
		height: 130px;
		background-image: url(../assets/myimg.jpg);
		background-size: cover;
		border-radius: 50%;
		border: white solid 6px;
		opacity: 0.95;
		margin: auto;
		margin-top: -68px;
	}
</style>


<script>
	import axios from 'axios';
	/* 导出组件,并为组件定义数据,函数,生命周期函数 */
	export default {
		data() {
			return {
				loginform: {
					username: '',
					password: ''
				},

				token:''
			}
		},


		methods: {
            handleKeydown(event) {//enter按键监听
                if (event.key === 'Enter') {
                    this.login();
                }
            },

			login() {
				if (this.loginform.username.length == 0 || this.loginform.password.length == 0) {
					this.$message({message: '账号密码不能为空!',type: 'error'});
					return;
				}

				axios.post("http://localhost:8080/login", this.loginform).then((res) => {
					if (res.data.msg == "账号密码错误") {
						this.$message.error('账号密码错误！');
					} else {
						console.log('Token:',res.data.data);
						this.token = res.data.data;
						localStorage.setItem("token",this.token);
						this.$message({ message: "欢迎登录，用户"+this.loginform.username, type: 'success' });
						this.$router.push("/stu");
					}
				}).catch(error => {
					console.error(error);
				});
			}
		},

        /*生命周期函数*/
        /*在组件创建完成并插入 DOM 后，进行某些初始化操作*/
        mounted() {
            window.addEventListener('keydown', this.handleKeydown);//将监听器添加到全局window对象，以监听键盘触发事件
        },

        /*在组件即将被销毁时，执行清理工作以避免内存泄漏*/
        beforeDestroy() {
            window.removeEventListener('keydown', this.handleKeydown);//卸载前，移除全局监听器，防止内存泄漏
        }
	}
</script>

